import * as IncrementalDOM from "incremental-dom";
import MarkdownItIncrementalDOM from "markdown-it-incremental-dom";
import "markdown-it-asciimath/ASCIIMathTeXImg.js";

import "highlight.js/styles/github.css";
import "../lib/katex/katex.css"

let hljs = require("highlight.js");

// var emojies_defs = require('../full_emoji.json');

// const string = require('string')
// const legacySlugify = s => string(s).slugify().toString()

let MarkdownIt = require("markdown-it")({
    html: true,
    linkify: true,
    typographer: true,
    langPrefix: true,
    quotes: "“”‘’",
    highlight: function (str, lang) {
      if (lang && hljs.getLanguage(lang)) {
        try {
          return (
            '<pre class="hljs"><code>' +
            hljs.highlight(lang, str, true).value +
            "</code></pre>"
          );
        } catch (e) {
          console.log(e);
        }
      }

      return (
        '<pre class="hljs"><code>' +
        MarkdownIt.utils.escapeHtml(str) +
        "</code></pre>"
      );
    }
  })
  .use(require("markdown-it-sub"))
  .use(MarkdownItIncrementalDOM, IncrementalDOM, {
    incrementalizeDefaultRules: false
  })
  .use(require("markdown-it-sup"))
  .use(require("markdown-it-footnote"))
  .use(require("markdown-it-mark"))
  .use(require("markdown-it-anchor"), {
    permalink: true,
    permalinkBefore: true,
    permalinkSymbol: "§"
  })
  .use(require("markdown-it-deflist"))
  .use(require("markdown-it-imsize"), {
    autofill: true
  })
  .use(require("markdown-it-ins"))
  .use(require("markdown-it-toc-done-right"))
  .use(require("markdown-it-charts"))
  .use(require("markdown-it-multimd-table"))
  .use(require("markdown-it-checkbox"), {
    divWrap: true,
    divClass: "cb",
    idPrefix: "cbx_"
  })
  .use(require("markdown-it-center-text"))
  .use(require("markdown-it-ins"))
  .use(require("markdown-it-abbr"))
  .use(require("markdown-it-attrs"))
  .use(require("markdown-it-meta"))
  .use(require("markdown-it-multimd-table"), {
    enableMultilineRows: true
  })
  // .use(require('markdown-it-texmath').use(require('katex')), {
  //   delimiters: 'dollars',
  //   macros: {
  //     "\\RR": "\\mathbb{R}"
  //   }
  // })
  .use(require("markdown-it-maths"));

// .use(require("markdown-it-asciimath"), {
//   useKeyword: false,
// })
// .use(require('markdown-it-include'), '../testInclude');

