import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "./plugins/ui.js";
import "./plugins/editor.js";
import "./plugins/icon.js";


// Global Style
import "./style/avocet.styl"
// import "./style/dark.styl"
import "./style/mdtheme/red.styl"

// import "./style/mdtheme/default.styl"


// Scroll sync
import ScrollSync from 'vue-scroll-sync';

import './registerServiceWorker'

