import 'emojione/extras/css/emojione.min.css'
import '../../style/font-awesome/all.min.css'
import {
    rendererRule,
    coreRuler
} from './utils/md-regex.js'
import emoji from './str2emoji.json'

import emojione from "emojione"

const IconsPlugin = (md, name) => {
    let options = null
    switch (name) {
        case 'emoji':
            const emojiRegex = new RegExp(`(:(?:[a-zA-Z_]+)\.{0,1}\\d{0,2}:)`)
            emojione.emojiSize = "128"

            options = {
                name,
                regex: emojiRegex,
                replace: (match) => {
                    var temp = match.split('.')
                    if (temp.length == 1) {
                        var emojiCode = temp[0]
                        var emojiSize = 2                        
                    } else {
                        var emojiCode = temp[0] + ":"
                        var emojiSize =temp[1].slice(0, -1) || 5
                    }
                    
                    return `<img style="height:${emojiSize}ex;transition: all 0.5s ease-in-out;"
                    class="emojione"
                    title = "${emojiCode}"
                    src = "https://cdn.jsdelivr.net/emojione/assets/4.0/png/128/${emoji[emojiCode]}.png" / > `
                }
            }
            break
        case 'font-awesome':
            const faRegex = new RegExp(`(:[s|r|b]{1}-(?:[a-zA-Z0-9-]+)\.\\d{1,2}#[a-fA-F0-9]{0,6}:)`)
            
            options = {
                name,
                regex: faRegex,
                replace: (match) =>  {
                    var temp = match.split('.')[0].slice(1)
                    var faSize = match.split('.')[1].split('#')[0]
                    var faColor = match.split('.')[1].split('#')[1].slice(0,-1)

                    return `<i class="fa${temp.slice(0,temp.indexOf('-'))} fa${temp.slice(temp.indexOf('-'))}" style="font-size:${faSize}ex;color:#${faColor};transition: all 0.5s ease-in-out;"></i>`
                } 
            }
            break
        default:
            return
    }
    md.renderer.rules[options.name] = (tokens, idx) => {
        return rendererRule(tokens, idx, options)
    }
    md.core.ruler.push(options.name, (state) => {
        coreRuler(state, options)
    })
}

export default IconsPlugin