<template>
    <div class="right-view">
        <Row type="flex" justify="center" align="middle" class="input-title-container">
            <Col span="24" class="input-title">
                <InputTitle :value="title"/>
            </Col>
        </Row>
        <Row type="flex" justify="center" align="middle" class="editor-toolbar-container">
            <Col span="24" class="editor-toolbar">
                <EditorToolbar @toolbar-clicked="$operate"/>
            </Col>
        </Row>
        <Row type="flex" justify="center" align="middle" class="editor-container">
            <Col span="24" class="editor">
                <Split v-model="split" max="10px" min="4px">
                    <div slot="left" class="editor-left">
                        <!-- <scroll-sync> -->
                        <codemirror
                            ref="editor"
                            @input="onCmCodeChange"
                            @scroll="triggerPreviewScroll"
                            @cursor-activity="cursorTriggerPreviewScroll"
                            :value="code"
                            :options="cmOptions"
                        />
                        <!-- </scroll-sync> -->
                    </div>
                    <div slot="right" class="editor-right">
                        <!-- <scroll-sync> -->
                        <div
                            id="markdown-body"
                            ref="preview"
                            @onscroll="$test"
                            class="preview-content"
                            style
                        />
                        <!-- </scroll-sync> -->
                    </div>
                    <div slot="trigger" class="ivu-split-trigger ivu-split-trigger-vertical">
                        <div class="ivu-split-trigger-bar-con vertical">
                            <i
                                class="ivu-split-trigger-bar"
                                v-once
                                v-for="i in 0"
                                :key="`trigger-${i}`"
                            />
                        </div>
                    </div>
                </Split>
            </Col>
        </Row>
    </div>
</template>

<script>
$(function() {
    // var reg = /^[\'\"]+|[\'\"]+$/g;
    // var chartjsLen = $(".chartjs").length;
    // var myChartJs = [];
    // for (var i = 0; i < chartjsLen; i++) {
    //     var ctx = $(".chartjs")[i].getContext("2d");
    //     myChartJs[i] = new Chart(
    //         ctx,
    //         $.parseJSON($($(".chartjs")[i])[0].innerHTML)
    //     );
    // }
});
import InputTitle from "@/components/InputTitle.vue";
import EditorToolbar from "@/components/EditorToolbar.vue";
import Debug from "@/assets/md/debug.md";
import Parser from "@/plugins/parser.js";

export default {
    name: "RightView",
    props: {
        doc: Object
    },
    data() {
        return {
            mdit: Parser.MarkdownIt,
            IncrementalDOM: Parser.IncrementalDOM,
            split: 0.5,
            title: "defauylt",
            code: ``,
            cmOptions: {
                tabSize: 2,
                mode: {
                    name: "gfm",
                    highlightFormatting: true
                },
                theme: "solarized",
                autoCloseTags: true,
                lineNumbers: false,
                lineWrapping: true,
                showHint: true,
                foldGutter: false,
                hint: {
                    anyword: true
                },
                // gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
                line: true,
                matchBrackets: true,
                scrollbarStyle: "overlay",
                lineWiseCopyCut: true,
                addModeClass: true,
                autoCloseBrackets: true,
                emoji: true,
                autofocus: true,
                gitHubSpice: false,
                highlightFormatting: true,
                showMatchesOnScrollbar: true,
                extraKeys: { Tab: "autocomplete" }
            },
            functions: {
                $Bold: this.$exec_Bold,
                $Italic: this.$exec_Italic,
                $Strikethrough: this.$exec_Strikethrough,
                $Heading: this.$exec_Heading,
                $Quote: this.$exec_Quote,
                $Link: this.$exec_Link,
                $Image: this.$exec_Image,
                $Code: this.$exec_Code,
                $Sup: this.$exec_Sup,
                $Sub: this.$exec_Sub,
                $Undo: this.$exec_Undo,
                $test: this.$test
            },
            scrollFlag: true
        };
    },
    created() {
        this.code = Debug;
        setTimeout(() => {
            this.codemirror.refresh();
        }, 100);
    },
    mounted() {
        // 通过$refs获取dom元素
        this.$renderMarkdown();

        this.$refs.preview.addEventListener(
            "scroll",
            () => {
                // let info = this.codemirror.getScrollInfo();
                // this.codemirror.scrollTo(
                //     null,
                //     (info.height - info.clientHeight) *
                //         (this.$refs.preview.scrollTop /
                //             this.$refs.preview.scrollHeight)
                // );
                // this.preview
                // this.codemirror.scrollTo(100,0)
                // let info = this.codemirror.getScrollInfo();
                //  =
                // (info.top / (info.height - info.clientHeight)) *
                // (this.$refs.preview.scrollHeight -
                // this.$refs.preview.clientHeight);
                // this.codemirror.scrollTo( this.$refs.preview.scrollTop/this.$refs.preview.scrollHeight  )
            },
            false
        );
    },
    updated() {
        this.$render_Chartjs();
    },
    computed: {
        codemirror() {
            return this.$refs.editor.codemirror;
        },
        compiledMarkdown() {
            return this.mdit.render(this.code);
        }
    },
    methods: {
        $operate() {
            // console.log("Toolbar Clicked: ", arguments[0]);
            this.functions[arguments[0].type](arguments[0].options);
        },
        // onCmReady(codemirror) {
        //     // console.log("the editor is readied!", codemirror);
        // },
        // onCmFocus(codemirror) {
        //     // console.log("the editor is focus!", codemirror);
        // },
        onCmCodeChange(newCode) {
            // console.log("content changed!");
            this.code = newCode;
            this.$renderMarkdown();
        },
        $renderMarkdown() {
            // const func = this.mdit.renderToIncrementalDOM(this.code)
            // console.log(document.getElementById('markdown-body'))
            this.IncrementalDOM.patch(
                document.querySelector("#markdown-body"),
                this.mdit.renderToIncrementalDOM(this.code)
            );
        },
        $render_Chartjs() {
            // var reg = /^[\'\"]+|[\'\"]+$/g;
            //- taucharts
            // var chartjsLen = $(".chartjs").length;
            // var myChartJs = [];
            // for (var i = 0; i < chartjsLen; i++) {
            //     var ctx = $(".chartjs")[i].getContext("2d");
            //     myChartJs[i] = new Chart(
            //         ctx,
            //         $.parseJSON($($(".chartjs")[i])[0].innerHTML)
            //     );
            // }
        },
        $test() {
            // console.log(arguments);
        },
        cursorTriggerPreviewScroll() {
            // console.log("asdasd", this.codemirror.doc.getCursor().line, this.codemirror.doc.lineCount());
            // this.$refs.preview.scrollTop =
            //     (this.codemirror.doc.getCursor().line / this.codemirror.doc.lineCount()) *
            //     (this.$refs.preview.scrollHeight -
            //         this.$refs.preview.clientHeight);
            // console.log(
            //     this.codemirror.getTokenAt(this.codemirror.doc.getCursor())
            // );
        },
        triggerPreviewScroll() {
            // let info = this.codemirror.getScrollInfo();
            // this.$refs.preview.scrollTop =
            //     (info.top / (info.height - info.clientHeight)) *
            //     (this.$refs.preview.scrollHeight -
            //         this.$refs.preview.clientHeight);
        },
        $exec_Bold() {
            let temp = this.codemirror.doc.getSelection();
            if (temp.replace(/ /g, "") == "") {
                return;
            }
            var r = this.codemirror.doc
                .getSelection()
                .match(new RegExp("\\*+\\*+.*\\*+\\*+"));
            if (!r) {
                this.codemirror.doc.replaceSelection(
                    "**" + temp + "**",
                    "around"
                );
            } else {
                this.codemirror.doc.replaceSelection(
                    temp.substring(2, temp.length - 2),
                    "around"
                );
            }
            this.codemirror.focus();
        },
        $exec_Italic() {
            let temp = this.codemirror.doc.getSelection();
            if (temp.replace(/ /g, "") == "") {
                return;
            }
            var r = this.codemirror.doc
                .getSelection()
                .match(new RegExp("\\*.*\\*"));
            if (!r) {
                this.codemirror.doc.replaceSelection(
                    "*" + temp + "*",
                    "around"
                );
            } else {
                this.codemirror.doc.replaceSelection(
                    temp.substring(1, temp.length - 1),
                    "around"
                );
            }
            this.codemirror.focus();
        },
        $exec_Strikethrough() {
            let temp = this.codemirror.doc.getSelection();
            if (temp.replace(/ /g, "") == "") {
                return;
            }
            var r = this.codemirror.doc
                .getSelection()
                .match(new RegExp("~+~+.*~+~+"));
            if (!r) {
                this.codemirror.doc.replaceSelection(
                    "~~" + temp + "~~",
                    "around"
                );
            } else {
                this.codemirror.doc.replaceSelection(
                    temp.substring(2, temp.length - 2),
                    "around"
                );
            }
            this.vcodemirror.focus();
        },
        $exec_Heading(level) {
            let anchor = this.codemirror.doc.getCursor();
            this.codemirror.doc.replaceRange(
                new Array(level + 1).join("#") + " ",
                { line: anchor.line, ch: 0 },
                { line: anchor.line, ch: 0 }
            );
            this.codemirror.focus();
        },
        $exec_Quote() {
            let anchor = this.codemirror.doc.getCursor();
            this.codemirror.doc.replaceRange(
                "> ",
                { line: anchor.line, ch: 0 },
                { line: anchor.line, ch: 0 }
            );
            this.codemirror.focus();
        },
        $exec_Link() {
            let temp = this.codemirror.doc.getSelection();
            this.codemirror.doc.replaceSelection(
                "[Alt Text](" + temp.replace(/ /g, "") + ")"
            );
            this.codemirror.focus();
        },
        $exec_Image() {
            let temp = this.codemirror.doc.getSelection();
            this.codemirror.doc.replaceSelection(
                "![Alt Text](" + temp.replace(/ /g, "") + ")\n"
            );
            this.codemirror.focus();
        },
        $exec_Code() {
            let anchor = this.codemirror.doc.getCursor();
            this.codemirror.doc.replaceRange(
                "```code\n\n``` ",
                { line: anchor.line, ch: 0 },
                { line: anchor.line, ch: 0 }
            );
            this.codemirror.focus();
            console.log(
                this.codemirror.doc.setSelection(
                    { line: anchor.line + 1, ch: 6 },
                    { line: anchor.line + 1, ch: 3 }
                )
            );
        },
        $exec_Undo() {
            this.codemirror.execCommand("undo");
        },
        $exec_Redo() {
            this.codemirror.execCommand("redo");
        },
        $exec_Sub() {
            let temp = this.codemirror.doc.getSelection();
            if (temp.replace(/ /g, "") == "") {
                return;
            }
            var r = this.codemirror.doc
                .getSelection()
                .match(new RegExp("~+.*~+"));
            if (!r) {
                this.codemirror.doc.replaceSelection(
                    "~" + temp + "~",
                    "around"
                );
            } else {
                this.codemirror.doc.replaceSelection(
                    temp.substring(2, temp.length - 2),
                    "around"
                );
            }
            this.codemirror.focus();
        },
        $exec_Sup() {
            let temp = this.codemirror.doc.getSelection();
            if (temp.replace(/ /g, "") == "") {
                return;
            }
            var r = this.codemirror.doc
                .getSelection()
                .match(new RegExp("\\^+.*\\^+"));
            if (!r) {
                this.codemirror.doc.replaceSelection(
                    "^" + temp + "^",
                    "around"
                );
            } else {
                this.codemirror.doc.replaceSelection(
                    temp.substring(2, temp.length - 2),
                    "around"
                );
            }
            this.codemirror.focus();
        }
    },
    components: {
        InputTitle,
        EditorToolbar
    }
};
</script>

