<template>
    <div class="root full-height">
        <Row type="flex" justify="start" align="middle" class="full-height">
            <Col span="12" class="full-height">
                <input :spellcheck="spellcheck" v-model="title" type="text">
            </Col>
        </Row>
    </div>
</template>

<script>
export default {
    name: "InputTitle",
    props: {
        value: {
            default: "New Post"
        },
        spellcheck: {
            default: false
        }
    },
    data() {
        return {
            i_value: this.value
        };
    },
    computed: {
        title: {
            get() {
                return this.i_value;
            },
            set(newValue) {
                this.i_value = newValue;
            }
        }
    },
    watch: {
        title() {
            console.log(arguments);
        },
        value() {
            this.i_value = arguments[0];
        }
    }
};
</script>

