import Vue from 'vue'


// Icon

import {
    library
  } from '@fortawesome/fontawesome-svg-core'
  import {
    faBold,
    faItalic,
    faHeading,
    faStrikethrough,
    faQuoteRight,
    faLink,
    faImage,
    faCode,
    faUndo,
    faRedo,
    faSuperscript,
    faSubscript,
    faBug
  } from '@fortawesome/free-solid-svg-icons'
  import {
    FontAwesomeIcon
  } from '@fortawesome/vue-fontawesome'
  require("vue2-animate/dist/vue2-animate.min.css");
  library.add(faBold,
    faItalic,
    faHeading,
    faStrikethrough,
    faQuoteRight,
    faLink, faImage,
    faCode,
    faUndo,
    faSuperscript,
    faSubscript,
    faBug,
    faRedo)
  
  Vue.component('font-awesome-icon', FontAwesomeIcon)
  
  